*,
::before,
::after {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box; /*兼容移动端主流浏览器*/
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent; /*清除移动端点击高亮效果*/
}

body {
  font-family: Microsoft YaHei, sans-serif; /*移动端默认的字体*/
  font-size: 14px;
  color: #333;
}

ol, ul {
  list-style: none;
}

/*清除浮动*/

.clearfix::before,
.clearfix::after {
  content: "";
  display: block;
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}

.layout {
  width: 100%;
  max-width: 750px;
  min-width: 320px;
  margin: 0 auto;
  position: relative;
}

.banner {
  width: 100%;
  height: 120px;
  overflow: hidden;
  position: relative;
}

.banner ul:first-child {
  width: 1000%;
  -webkit-transform: translateX(-10%);
  transform: translateX(-10%);
}

.banner ul:first-child li {
  width: 10%;
  float: left;
}

.banner ul:first-child li a {
  text-align: center;
  display: block;
  width: 100%;
}

.banner ul:first-child li a img {
  width: 100%;

  display: inline-block;
}

.banner ul:last-child {
  position: absolute;
  bottom: 6px;
  width: 100%;
  text-align: center;
}

.banner ul:last-child li {
  width: 6px;
  height: 6px;
  border: 1px solid #fff;
  border-radius: 50%;
  display: inline-block;
  margin-left: 10px;
}

.banner ul:last-child li:first-child {
  margin-left: 0;
}

.banner ul:last-child li.now {
  background: #fff;
}
.tab a{
  display: block;
